.separator {
    margin: 0.25em;
}
.name {
    display: flex;
    align-items: center;
    font-size: smaller;
}
.spinner {
    position: fixed;
    top: 0px;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: orange;
    animation: spin 2s linear infinite;
}
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
